import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { JeuVideo } from 'src/app/services/interfaces/jeu-video';
import { DataJeuVideoService } from 'src/app/services/data-jeu-video.service';
import { NavController } from '@ionic/angular';

@Component({
	selector: 'app-info-jeu-video',
	templateUrl: './info-jeu-video.page.html',
	styleUrls: [ './info-jeu-video.page.scss' ]
})
export class InfoJeuVideoPage implements OnInit {
	selectedJeuVideo: JeuVideo;

	constructor(
		private navCtrl: NavController,
		private serviceJeuVideo: DataJeuVideoService,
		private activatedRoute: ActivatedRoute
	) {}

	ngOnInit() {
		this.activatedRoute.paramMap.subscribe((paramMap) => {
			if (!paramMap.has('idJeuVideo')) {
				this.navCtrl.navigateBack([ '/medias/jeux-video' ]);
				return;
			}
			this.selectedJeuVideo = this.serviceJeuVideo.getInMyJeuVideo(paramMap.get('idJeuVideo'));
		});
	}
	goBack() {
		this.navCtrl.navigateBack([ '/medias/jeux-video' ]);
	}
}
